import React, { useState } from 'react';
import { Meta, StoryObj } from '@storybook/react';
import Drawer from './Drawer';
import Button from '../button/Button';

const meta: Meta<typeof Drawer> = {
  title: 'UI/Drawer',
  component: Drawer,
  tags: ['autodocs'],
  argTypes: {
    placement: {
      control: 'select',
      options: ['left', 'right', 'top', 'bottom'],
    },
    isOpen: { control: 'boolean' },
    drawerColor: { control: 'color' },
    backdropColor: { control: 'color' },
    backdropOpacity: { control: { type: 'range', min: 0, max: 1, step: 0.1 } },
    closeOutSide: { control: 'boolean' },
    width: { control: 'text' },
  },
};

export default meta;

type Story = StoryObj<typeof Drawer>;

const DrawerTemplate: Story = {
  render: (args) => {
    // eslint-disable-next-line react-hooks/rules-of-hooks
    const [isOpen, setIsOpen] = useState(false);
    return (
      <div>
        <Button shape="rounded" color="primary" onClick={() => setIsOpen(true)}>Open Drawer</Button>
        <Drawer {...args} isOpen={isOpen} setIsOpen={setIsOpen}>
          <div className="p-4">
            <h2 className="text-lg font-bold mb-2">Drawer Content</h2>
            <p>This is the content of the drawer.</p>
            <button onClick={() => setIsOpen(false)} className="mt-4 bg-blue-500 text-white px-4 py-2 rounded">
              Close Drawer
            </button>
          </div>
        </Drawer>
      </div>
    );
  },
};

export const Default: Story = {
  ...DrawerTemplate,
  args: {
    placement: 'left',
    closeOutSide: true,
  },
};

export const RightPlacement: Story = {
  ...DrawerTemplate,
  args: {
    placement: 'right',
    closeOutSide: true,
  },
};

export const TopPlacement: Story = {
  ...DrawerTemplate,
  args: {
    placement: 'top',
    closeOutSide: true,
  },
};

export const BottomPlacement: Story = {
  ...DrawerTemplate,
  args: {
    placement: 'bottom',
    closeOutSide: true,
  },
};

export const CustomColors: Story = {
  ...DrawerTemplate,
  args: {
    placement: 'left',
    closeOutSide: true,
    drawerColor: '#f0f0f0',
    backdropColor: '#000000',
    backdropOpacity: 0.5,
  },
};

export const CustomWidth: Story = {
  ...DrawerTemplate,
  args: {
    placement: 'left',
    closeOutSide: true,
    width: '300px',
  },
};

export const NoCloseOutside: Story = {
  ...DrawerTemplate,
  args: {
    placement: 'left',
    closeOutSide: false,
  },
};